'use server';

function formatGetPostParam(postType) {
    if (postType === 0) {
        return 'qna/class=0';
    } else if (postType === 1) {
        return 'codeshare';
    } else if (postType === 2) {
        return 'daily';
    } else {
        return new Error('Invalid post type');
    }
}


export async function fetchPostByTypeDB(postType) {
    const postTypeParam = formatGetPostParam(postType);
    try {
        const cardsData = await fetch(`/api/post/${postTypeParam}`, {
            method: 'GET',
        }).then(r => r.json());
        return cardsData;
    } catch (error) {
        console.error('Error:', error);
    }
}
